const isPro = Object.is(process.env.NODE_ENV, 'production');
let API,FEIHUO;
if(isPro){
  API = 'http://'+document.domain;
  FEIHUO = 'http://www.feihuo.com';
}else{
  API = '/api';
  FEIHUO = '/feihuo';
}
export default {
  homeurl:API+'/h5Api/index/cmd/getPageBase/v/2',//
  gameListUrl:API+'/h5Api/index/cmd/getGameList',//
  getSubjectInfo:API+'/h5Api/index/cmd/getSubjectInfo',//
  getGameInfo:API+'/h5Api/index/cmd/getGameInfo',//
  getArticleList:API+'/h5Api/index/cmd/getArticleList',//
  getArticleDetail:API+'/h5Api/index/cmd/getArticleDetail',//
  getGiftCode:FEIHUO+'/gift/getGiftCode',//
  getCouponList:API+'/h5Api/index/cmd/getCouponList',//
  getThirdLoginUrl:API+'/h5Api/index/cmd/getThirdLoginUrl',//
  getUserInfo:API+'/h5Api/index/cmd/getUserInfo',//
  login:API+'/h5Api/index/cmd/login',//
  getRecommendInfo:API+'/h5Api/index/cmd/getRecommendInfo',//
  register:API+'/h5Api/index/cmd/register',//
  sendSmsCode:API+'/h5Api/index/cmd/sendSmsCode',//
  logout:API+'/login/logout',//
  cardAuth:API+'/h5Api/index/cmd/cardAuth',//
  bindMobile:API+'/h5Api/index/cmd/bindMobile',//
  getSpecialSign:API+'/h5Api/index/cmd/getSpecialSign',//
}



