// mockjs  
const Mock = require('mockjs');  
//  mock.Random   
const Random = Mock.Random;  
// mock  
const productData = function() {  
  let Data = [];  
  for (let i = 0; i < 100; i++) {  
    let data = {  
      name: Random.cname(), // Random.cname()   
      date: Random.date()  // Random.date(),yyyy-MM-dd  
    }  
    Data.push(data)  
  }  
  return {   
    "success":1,
    "data":Data
  }  
}  

// mock
const game = {
    "success":1,
    "data|20":[
      {
        "id|1-100":1,
        "imgsrc":require("img/1.png"),
        "name":'@ctitle(2,9)',
        "intro":'@ctitle(1,40)',
     }
    ]
}

  
// Mock.mock( url, post/get , )  
Mock.mock('/user/login', 'post', productData );  
Mock.mock('/user/query', 'post', game);  


